.exam-page {
  .active {
    color: #2672ff;
  }

  .blueBorder {
    border: 1px solid #2672ff;
  }

  .blueBg {
    background-color: #2672ff;
    color: white;
  }

  p,
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  ol,
  ul,
  dl {
    margin-bottom: 0;
  }

  .exam-header {
    width: 100%;
    height: 64px;
    background-color: white;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .08);
    position: fixed;
    left: 0;
    top: 0;
    z-index: 10;
    display: flex;
    padding: 0 10px 0 66px;
    justify-content: space-between;
    align-items: center;

    .backToList {
      position: absolute;
      left: 24px;
      top: 20px;
      width: 18px;
      height: 18px;
      font-size: 18px;
      cursor: pointer;
    }

    .exam-step {
      width: 300px;

      .ant-steps-navigation {
        user-select: none;
      }

      .ant-steps-navigation .ant-steps-item.ant-steps-item-active::before {
        width: 65%;
        bottom: -4px;
        left: 7px;
      }

      .ant-steps-navigation .ant-steps-item::after {
        margin-top: -10px;
        margin-left: -7px;
        width: 8px;
        height: 8px;
      }

      .ant-steps-navigation .ant-steps-item:nth-child(n+2).ant-steps-item-active::before {
        width: 56%;
        left: 23px;
      }

      .ant-steps-item-icon {
        display: none;
      }

      .ant-steps-item-finish>.ant-steps-item-container>.ant-steps-item-content>.ant-steps-item-title {
        color: #2672ff;
        font-size: 14px;
      }

      .ant-steps-item-wait>.ant-steps-item-container>.ant-steps-item-content>.ant-steps-item-title {
        font-size: 14px;
        color: rgba(0, 0, 0, .85);
      }
    }

    .save-issue {
      padding-right: 15px;

      .iconfont {
        cursor: pointer;
      }
    }
  }

  .exam-content {
    padding: 0;
    width: 100%;
    margin-top: 64px;
    height: calc(100% - 64px);
    overflow-y: auto;

    .left-nav {
      width: 280px;
      height: 100%;
      position: fixed;
      left: 0;
      top: 64px;
      box-shadow: 0 2px 8px 0 rgba(0, 0, 0, .16);
      background-color: white;
    }

    .exam-body {
      padding: 18px 278px 30px 300px;
      width: 100%;
      overflow-x: hidden;

      .inner {
        width: 100%;
        min-width: 800px;
        // .normal-question-list{

        // }
        .exam-titleAndWelcome {
          position: relative;
          margin-bottom: 12px;
          padding: 18px 80px 18px 70px;
          border-radius: 4px;
          background-color: #fff;
          box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .1);

          .title-text:focus {
            background-color: rgb(244, 244, 244);
          }

          .title-wrap {
            // height: 44px;

            .title-text {
              // height: 100%;
              width: 100%;
              font-size: 20px;
              padding: 6px 10px;
            }
          }

          .welcome-wrap {
            // height: 40px;

            .title-text {
              // height: 100%;
              width: 100%;
              font-size: 18px;
              padding: 6px 10px;
              color: #aaa;
            }
          }

          .total-score {
            position: absolute;
            right: 12px;
            top: 24px;
            border-radius: 4px;
            min-width: 65px;
            min-height: 65px;
            background-color: #00bf6f;
            color: white;

            &>* {
              margin: 0;
              padding: 0;
            }

            h3 {
              margin-top: 2px;
              text-align: center;
              font-weight: 500;
              font-size: 14px;
              color: #fff;
            }

            p {
              text-align: center;
              font-weight: 700;
              font-size: 30px;
              color: #fff;
            }
          }
        }

        .question-item {
          width: 100%;
          background-color: white;
          // position: relative;
          // z-index: -2;
          margin: 12px 0;
          border-radius: 4px;
          box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .1);
          padding: 18px 40px 30px 30px;
          border: 1px solid transparent;

          &.question_focus {
            margin: 24px 0;
            border-color: #2672ff;
            box-shadow: 0 6px 12px 0 rgba(0, 0, 0, .1);
            .ques-content{
              .addOption{
                display: block;
              }
              .title .text-wrap .operation{
                display: flex;
              }
            }
          }

          &:hover .ques-content .text-wrap .operation {
            display: flex;
          }

          .ques-content {
            .ant-upload-list-item {
              display: none;
            }

            .text-wrap {
              position: relative;

              &+.must-fill.normal {
                top: 3px;
                height: 36px;
              }

              input[type='text'] {
                width: 800px;
                height: 100%;
                font-size: 16px;
                padding: 6px 10px;
                border: 1px solid transparent;

                &:hover {
                  border: 1px dashed #ccc;
                }

                &:focus {
                  border: 1px solid transparent;
                  background-color: #f4f4f4;
                }
              }

              &:hover,
              &:focus {
                .option-tools {
                  display: flex;
                }
              }

              .option-tools {
                position: absolute;
                top: 0;
                left: 794px;
                height: 33px;
                width: 80px;
                padding-left: 20px;
                display: none;
                align-items: center;

                .iconfont {
                  font-size: 18px;
                  margin-right: 15px;

                  &:hover {
                    color: #2672ff;
                    cursor: pointer;

                    &.icon-jianshao {
                      color: red;
                    }
                  }
                }
              }

              .operation {
                position: absolute;
                width: 150px;
                right: 10px;
                top: 0;
                height: 33px;
                display: none;
                justify-content: space-around;
                align-items: center;

                span {
                  font-size: 16px;
                  padding: 5px;

                  &:hover {
                    color: #2672ff;
                    cursor: pointer;

                    &.icon-shanchu {
                      color: red;
                    }
                  }
                  &.iconfont.isInStore{
                    color: #2672ff;
                    font-weight: bold;
                  }
                }
              }
            }

            .title {
              padding-left: 50px;
              position: relative;
              font-size: 16px;

              .must-fill {
                position: absolute;
                font-size: 16px;
                left: 20px;
                // left: 0;
                // transform: translateX(-100%);
                top: -1px;

                .red {
                  color: red;
                }

                .index {
                  font-size: 17px;
                }
              }
            }

            .img-container {
              position: relative;
              padding-right: 40px;

              .remove {
                display: none;
                position: absolute;
                width: 30px;
                height: 30px;
                line-height: 30px;
                text-align: center;
                right: 5px;
                top: 50%;
                transform: translateY(-50%);
                color: red;
                cursor: pointer;
              }

              &:hover {
                img+.remove {
                  display: block;
                }
              }
            }

            .option-item {
              .container {
                width: 800px;
                position: relative;

                .showInp.filling {
                  border: 1px dashed transparent;

                  &:hover {
                    border-color: #d8d8d8;
                  }

                  &:focus {
                    border-color: transparent;
                  }

                }

                &:hover {
                  .option-tools {
                    display: flex;
                  }
                }

                input:focus+.option-tools {
                  display: flex;
                }

                .option-tools {
                  position: absolute;
                  top: 13px;
                  right: -100px;
                  height: 33px;
                  width: 80px;
                  padding-left: 20px;
                  display: none;
                  align-items: center;

                  .iconfont {
                    font-size: 18px;
                    margin-right: 15px;

                    &:hover {
                      color: #2672ff;
                      cursor: pointer;

                      &.icon-jianshao {
                        color: red;
                      }
                    }
                  }
                }
              }

              input[type='text'] {
                width: 780px;

                height: 100%;
                font-size: 16px;
                padding: 6px 10px;

                &.showSerial {
                  padding: 6px 10px 6px 25px;
                }

                margin-left: 4px;
                position: relative;
                z-index: 1;

                &:focus {
                  background-color: #f4f4f4;

                  &.inputOption {
                    border: none;
                  }

                  &+.hiddenHolder {
                    display: none;

                    &+.option-tools {
                      display: flex;
                    }
                  }
                }

                &.filling {
                  margin-left: 50px;
                  border: 1px solid transparent;

                  &+.option-tools {
                    right: -124px;
                  }
                }
              }

              &>input[type='radio'].showInp,
              &>input[type='checkbox'].showInp {
                line-height: 36px;
                vertical-align: middle;
                margin-top: -2px;

              }

              label {
                position: relative;
                display: inline-block;
                height: 36px;

                // line-height: 36px;
                input[type='text'] {
                  border: 1px solid transparent;
                }

                &:hover {
                  input[type='text'] {
                    border: 1px dashed #ccc;
                  }

                  .option-tools {
                    display: flex;
                  }
                }

                .hiddenHolder {
                  position: absolute;
                  left: 12px;
                  top: 50%;
                  transform: translateY(-50%);
                  height: 33px;
                  line-height: 32px;
                  font-size: 16px;
                  z-index: 0;
                  overflow: hidden;
                  padding-right: 90px;

                  .serial {
                    position: absolute;
                    left: 0px;
                    top: 50%;
                    transform: translateY(-50%);
                  }

                  .placeHolder {
                    // position: absolute;
                    height: 33px;
                    // left: 0;
                    // top: 50%;
                    // transform: translateY(-50%);
                    float: left;
                    opacity: 0;
                    padding-right: 15px;
                  }
                }

                .option-tools {
                  position: absolute;
                  top: 0px;
                  right: -70px;
                  height: 33px;
                  width: 80px;
                  padding-left: 20px;
                  display: none;
                  align-items: center;

                  .iconfont {
                    font-size: 18px;
                    margin-right: 15px;

                    &:hover {
                      color: #2672ff;
                      cursor: pointer;

                      &.icon-jianshao {
                        color: red;
                      }
                    }
                  }
                }
              }

              &>.filling {
                .inputOption {
                  margin-top: 15px;
                  margin-left: 50px;
                  border: 1px solid #d8d8d8;
                  position: relative;
                  z-index: 1;
                }
              }
            }

            .showInp {
              &[type='radio'] {
                width: 15px;
                height: 15px;
              }

              &[type='text'] {
                width: 440px;
                height: 32px;
              }

              margin: 12px 0 0 51px;
              padding: 5px 10px;

              border: 1px solid #d8d8d8;
              border-radius: 2px;
            }
            .addOption{
              height: 30px;
              display: none;
            }
          }
        }
      }
    }

    .right-nav {
      width: 260px;
      height: 100%;
      position: fixed;
      right: 0;
      top: 64px;
      box-shadow: 0 2px 8px 0 rgba(0, 0, 0, .16);
      z-index: 9;
      background-color: white;
    }

    .issue-box {
      .not-issue {
        .title {
          font-size: 22px;
          margin-bottom: 24px;
        }
      }

      .issued {
        padding-bottom: 20px;

        .container {
          width: 800px;
          margin: 0 auto;

          .title {
            height: 40px;
            line-height: 40px;

            &>span {
              display: inline-block;
              vertical-align: middle;
            }
          }

          .title-text {
            font-size: 22px;
          }

          .describe {
            position: relative;
            top: 1px;
            padding-left: 5px;
            font-size: 16px;
            color: #aaa;
          }

          .issue-share {
            margin-top: 15px;
            height: 200px;
            background-color: white;
            border-radius: 15px;
            box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .08);
            display: flex;
            justify-content: space-between;
            align-items: center;

            .link {
              flex: 1;
              height: 200px;
              padding: 25px 30px 36px 30px;

              h3 {
                margin-bottom: 15px;
              }

              .link-text {
                width: 580px;
                height: 40px;

                &>* {
                  height: 40px;
                  border: 1px solid;
                  // cursor: not-allowed;
                }

                input {
                  width: 355px;
                  padding-left: 10px;
                  border-color: #d8d8d8;
                  background: #f7f8fa;
                  border-right: none;
                }
              }
            }

            .code {
              flex-shrink: 0;
              width: 160px;
              height: 200px;
              border-left: 1px solid #eaeaea;
            }
          }
        }
      }
    }
  }
}

.ant-popover-inner-content {
  cursor: pointer;

  .home-logout:hover {
    color: #2672ff;
  }
}

.type-window {
  width: 185px;
  ol,
  ul,
  dl {
    margin-bottom: 0;
  }
  ul{
    padding-bottom: 5px;
    border-bottom: 1px solid #ccc;
  }
  li{
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: default;
    font-size: 14px;
    padding: 3px 8px;
    &:hover{
      background-color: #f7f8fa;
    }
    .add-type{
      color: #2672ff;
      cursor: pointer;
    }
  }
  .add-new-type{
    display: flex;
    padding-top: 5px;
    margin-top: 5px;
    justify-content: center;
    align-items: center;
    .iconfont{
      margin-right: 5px;
    }
    text-align: center;
    &:hover{
      color: #2672ff;
    }
  }
}