.exam-right-box {
  width: 100%;
  height: 100%;
  overflow-y: auto;

  &>.title {
    width: 100%;
    height: 42px;
    line-height: 42px;
    text-align: center;
    color: #2672ff;
    font-size: 16px;
  }

  .question-info {
    padding: 15px 0 0 18px;

    h2.type {
      font-size: 20px;
      font-weight: bold;
      margin-bottom: 14px;

      &+.set {
        padding: 14px 0;
        font-size: 14px;
        line-height: 14px;
        font-weight: bold;
        border-bottom: 1px solid #eaeaea;
      }
    }

    .changeType {
      box-sizing: content-box;
      height: 38px;
      padding: 6px 18px 6px 0;
      line-height: 38px;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    .answer{
      padding: 6px 18px 6px 0;
      &>p{
        margin-bottom: 6px;
        font-size: 12px;
        color: #707070;
      }
    }
    .score{
      padding: 6px 18px 18px 0;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .padding{
        margin-left: 5px;
      }
    }
    .option-type{
      border-top: 1px solid #eaeaea;
      padding-top: 18px;
      &>p{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-right: 22px;
      }
    }
  }
}