.left-nav-box {
  color: #707070;

  .tab-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;

    span {
      width: calc(100% / 3);
      height: 40px;
      line-height: 40px;
      text-align: center;
      border: 1px solid #eaeaea;
      border-top-color: transparent;
      border-right: none;
      font-size: 13px;
      background: #f7f8fa;
      cursor: pointer;
      letter-spacing: 1px;
      user-select: none;

      &.active {
        color: #2672ff;
        background-color: white;
        border-bottom: none;
      }
    }
  }

  .nav-body {
    .type-list {
      width: 100%;
      height: 100%;
      padding-left: 12px;
      overflow-y: auto;
      color: #484848;

      .list-title {
        margin: 18px 0 10px;
        font-weight: 400;
        font-size: 16px;
        padding: 0 20px 0 14px;
      }

      .list-content {
        color: #707070;
        font-size: 12px;
        display: flex;
        justify-content: space-between;
        padding: 0 20px 0 14px;
        flex-wrap: wrap;

        .icon-chakan:hover {
          color: #2672ff;
        }

        .ant-btn-default:focus {
          color: rgba(0, 0, 0, 0.85);
          border-color: #d9d9d9;
        }

        .ant-btn-default:hover {
          color: #40a9ff;
          border-color: #40a9ff;
        }

        &>* {
          margin-right: 10px;
          margin-bottom: 10px;
          margin-top: 10px;

          .iconfont {
            margin-left: 20px;
            margin-right: 20px;
          }

          .iconfont,
          .itemName {
            display: inline-block;
            // margin: 0 2px;

            &.icon-jianda2 {
              margin-left: 15px;
              margin-right: 15px;

              &+.itemName {
                margin-left: -1px;
              }
            }
          }

          .itemName {
            font-size: 15px;
            position: relative;
            top: -1px;
          }
        }
      }
    }

    .question-store {
      width: 100%;
      height: 100%;
      padding: 12px;

      .store-module {
        margin-top: 4px;
        width: 100%;
        height: 40px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-bottom: 1px solid #eaeaea;

        h2 {
          font-size: 14px;
          font-weight: bold;
        }

        .operation {
          color: #2672ff;
          font-size: 12px;

          .new-store {
            margin-right: 12px;
          }
        }
      }

      .store-list-module {
        width: 100%;
        height: calc(100vh - 200px);
        padding: 4px 0;
        padding-right: 8px;
        border-bottom: 1px solid #eaeaea;
        overflow-y: auto;

        .question-item-search {
          &:hover {
            background-color: rgb(247, 248, 250);
          }

          .highLight {
            color: #2672ff;
          }
        }

        .site-collapse-custom-collapse .site-collapse-custom-panel .ant-collapse-content-box {
          padding-bottom: 8px;
        }

        &::-webkit-scrollbar {
          padding: 0;
          width: 8px;
          height: 8px;
        }

        &::-webkit-scrollbar-thumb {
          border-left: 2px solid rgba(128, 133, 144, .06);
          border-right: 2px solid rgba(128, 133, 144, .06);
          border-radius: 6px;
          width: 8px;
          background: rgba(128, 133, 144, .15);
        }

        [data-theme='compact'] .site-collapse-custom-collapse .site-collapse-custom-panel,
        .site-collapse-custom-collapse .site-collapse-custom-panel {
          overflow: hidden;
          background: #fff;
          border: 0px;
          border-radius: 2px;
          position: relative;
          font-size: 12px;

          .count {
            position: absolute;
            right: 0;
            top: 50%;
            transform: translateY(-50%);
          }


          .ant-collapse-header {
            line-height: 26px;
            padding: 0;
            color: #707070;

            &:hover {
              background-color: rgb(247, 248, 250);
            }
          }

          .ant-collapse-content-box {
            padding-left: 0;
            padding-right: 0;
          }

          .store-question-list {
            .question-item {
              cursor: pointer;
              height: 26px;
              line-height: 26px;
              padding-left: 25px;
              position: relative;

              &:hover {
                background-color: rgb(247, 248, 250);

                .view {
                  display: block;
                }
              }

              .view {
                position: absolute;
                display: none;
                right: 0;
                top: 50%;
                transform: translateY(-50%);

                &:hover {
                  color: #2672ff;
                }
              }
            }
          }
        }
      }
    }

    .outline {
      padding: 10px;
      overflow-y: auto;
      height: calc(100vh - 100px);

      &::-webkit-scrollbar {
        padding: 0;
        width: 8px;
        height: 8px;
      }

      &::-webkit-scrollbar-thumb {
        border-left: 2px solid rgba(128, 133, 144, .06);
        border-right: 2px solid rgba(128, 133, 144, .06);
        border-radius: 6px;
        width: 8px;
        background: rgba(128, 133, 144, .15);
      }

      &>ul>li {
        width: 100%;
        padding: 4px 10px;
        font-size: 12px;
        cursor: pointer;
        color: #484848;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;

        &:hover {
          background: #f7f8fa;
        }
      }
    }
  }
}

.store-question-preview {

  // border: 1px solid ;
  ul {
    margin-bottom: 0;
  }

  .preview-question {
    min-width: 200px;
    padding: 0 10px;

    .preview-title {
      font-size: 16px;
      margin-bottom: 10px;
    }
  }

  textarea {
    margin: 10px 0;
    resize: none;
    border: 1px solid #ccc;
  }
}

.store-manage-window {
  display: flex;
  // width: 100%;
  min-height: 500px;

  .store-type-list {
    width: 246px;
    height: 500px;
    padding: 18px 0;
    flex-shrink: 0;
    border-right: 1px solid #f0f0f0;

    li {
      height: 28px;
      padding: 0 20px;
      line-height: 28px;
      font-size: 12px;
      cursor: pointer;

      &.focus {
        background-color: #f7f8fa;
      }

      &:hover {
        background-color: #f7f8fa;

        .store-operation {
          display: inline;
        }
      }

      .store-count {
        padding: 0 10px;
        color: #aaa;
      }

      .store-operation {
        float: right;
        display: none;

        &:hover {
          color: #2672ff;
        }
      }
    }
  }

  .store-question-list {
    flex-grow: 1;
    padding: 18px 20px;
    height: 500px;
    overflow-y: auto;

    &::-webkit-scrollbar {
      padding: 0;
      width: 8px;
      height: 8px;
    }

    &::-webkit-scrollbar-thumb {
      border-left: 2px solid rgba(128, 133, 144, .06);
      border-right: 2px solid rgba(128, 133, 144, .06);
      border-radius: 6px;
      width: 8px;
      background: rgba(128, 133, 144, .15);
    }

    .question-item {
      position: relative;
      padding: 12px 18px;
      margin-bottom: 12px;
      border: 1px solid #f0f0f0;

      .question-type {
        padding: 1px 6px;
        font-size: 12px;
        // width: 48px;
        display: inline-block;
        height: 20px;
        background-color: rgba(38, 114, 255, .08);
        color: rgba(38, 114, 255, .7);
      }

      .question-name {
        margin: 8px 0 8px 2px;
        font-size: 14px;
      }

      .option {
        padding-left: 17px;

        p {
          display: flex;
          align-items: center;
          margin-bottom: 5px;
          font-size: 14px;

          textarea {
            resize: none;
            border: 1px solid #eaeaea;
          }
        }
      }

      .show-flag {
        display: inline-block;
        font-size: 12px;
        color: #707070;
        margin-top: 10px;
        margin-left: 3px;

        .iconfont {
          margin-left: 5px;
          font-size: 10px;
          // &.show{
          //   transform: rotate(180deg);
          // }
        }
      }

      &:hover {
        .icon-shanchu {
          display: block;
        }
      }

      .icon-shanchu {
        display: none;
        cursor: pointer;
        position: absolute;
        right: 15px;
        top: 10px;

        &:hover {
          color: red;
        }
      }
    }

    &.empty {
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
}

.store-manage-window-type-operation {
  .ant-popover-inner-content{
    padding: 4px;
  }
  .store-manage-window-type-operation-list {
    li {
      padding: 2px 12px;
      &:hover {
        background-color: #eaeaea;
      }
    }
  }
}